body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header-container{
  padding: 20px;
  background-color: black;
  color: white;
  text-align: center;
}

.header-text{
  font-weight: bold;
  font-size: 25px;
  letter-spacing: 4px;
}

.main-container{
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  
}
.quiz-start-container{
  display: flex;
  gap: 30px;
  flex-direction: column;
}
.quiz-start-container div{
  font-size: 25px;
  font-weight: 100;
}
.quiz-start-container button, .retry{
  align-self: center;
  outline: none;
  border: none;
  border-radius: 3px;
  padding: 8px 25px;
  background-color: rgb(0, 0, 51);
  color : white;
  cursor: pointer;
}

.quiz-container{
  border: gray 1px solid;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  gap: 25px;
  flex-direction: column;
}
.question{
  font-weight: bold;
  font-size: 18px;
}

.options{
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.option{
  border-radius: 2px;
  padding: 3px;
}

.options *{
  cursor:pointer;
}
.options input{
  margin-right: 10px;
}

.next-button{
  padding: 10px;
  background-color: rgb(29, 29, 29);
  color: white;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
}

.result-container{
  text-align : center;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.result-container:first-child, .result-container:nth-child(2){
  font-size: 23px;
  font-weight: 100;
}

.score{
  font-weight: bold;
  font-size: 30px;
}

.retry{
  margin-top: 20px;
}